import { Switch, Route } from "react-router-dom";
import "./App.sass";
import NewGradient from "./component/new/NewGradient";
import Home from "./component/home/Home";
import EditGradient from "./component/edit/EditGradient";
const pblIrl = "http://apekhnik.github.io/gradient";
function App() {
  return (
    <div className="App">
      <div className="content">
        <Switch>
          <Route path={pblIrl + "/"} exact component={Home} />
          <Route path={pblIrl + "/new"} component={NewGradient} />
          <Route path={pblIrl + "/edit/:id?"} component={EditGradient} />
        </Switch>
      </div>
    </div>
  );
}

export default App;
